<template>
    <div class="datamanager">
      <!--  -->
      <div class="toolbox" @click="toggleToolbox">
        <img :src="toolboxIcon" alt="Toolbox Icon" class="toolbox-icon">
        <span class="toolbox-text"></span>
      </div>
      <!--  -->
      <div v-if="showToolboxPopup" class="toolbox-popup">
        <div class="toolbox-grid">
          <div class="toolbox-item" @click="openLoadDataPopup" @mouseover="startTooltipTimer('', '', $event)" @mouseleave="hideTooltip">
            <img src="@/assets/image/gis_dev_zgw_img/add_data.png" alt="">
          </div>
          <div class="toolbox-item" @click="toggleTimeSlider" :class="{ active: !timeSliderDisabled }" @mouseover="startTooltipTimer('', '', $event)" @mouseleave="hideTooltip">
            <img src="@/assets/image/gis_dev_zgw_img/time_slider.png" alt="">
          </div>
          <div class="toolbox-item" @mouseover="startTooltipTimer('', '', $event)" @mouseleave="hideTooltip">
            <img src="@/assets/image/gis_dev_zgw_img/filter.png" alt="">
          </div>
          <div class="toolbox-item" @click="toggleFeatureTable" @mouseover="startTooltipTimer('', '', $event)" @mouseleave="hideTooltip">
            <img src="@/assets/image/gis_dev_zgw_img/deal_data.png" alt="">
          </div>
          <div class="toolbox-item" @click="openKernelDensityPopup" @mouseover="startTooltipTimer('', '', $event)" @mouseleave="hideTooltip">
            <img src="@/assets/image/gis_dev_zgw_img/dot_density.png" alt="">
          </div>
          <div class="toolbox-item" @mouseover="startTooltipTimer('', '', $event)" @mouseleave="hideTooltip">
            <img src="@/assets/image/gis_dev_zgw_img/select.png" alt="">
          </div>
        </div>
        <!--  -->
        <span class="toolbox-close" @click="toggleToolbox">
          <img src="@/assets/image/map_icon/cancel_dark.png" alt="">
        </span>
      </div>
      <!-- 1 -->
      <div v-if="showLoadDataPopup" class="modal-overlay">
      <div class="modal-content">
        <h2></h2>
        <div>
          <label for="dataSelect"></label>
          <select id="dataSelect" class="styled-select">
            <option value="1">1</option>
            <option value="2">2</option>
            <option value="3">3</option>
          </select>
        </div>
          <div class="modal-buttons">
            <button @click="confirmLoadData"></button>
            <button @click="cancelLoadData"></button>
          </div>
        </div>
      </div>
      <div v-if="showKernelDensityPopup" class="modal-overlay">
    <!-- 2 -->
    <div class="modal-content">
      <h2></h2>
      <div>
        <label for="kernelRadius"></label>
        <input type="number" id="kernelRadius" v-model="kernelRadius" class="styled-select">
      </div>
      <div>
        <label for="weightField"></label>
        <select id="weightField" v-model="weightField" class="styled-select">
          <option v-for="field in availableFields" :key="field" :value="field">{{ field }}</option>
        </select>
      </div>
      <div>
        <label for="maxValue"></label>
        <input type="number" id="maxValue" v-model="maxValue" class="styled-select">
      </div>
      <!-- <div>
        <label for="colorStops"></label>
        <input type="text" id="colorStops" v-model="colorStops" placeholder="" class="styled-select">
      </div> -->
      <div class="modal-buttons">
        <button @click="applyKernelDensity"></button>
        <button @click="cancelKernelDensity"></button>
      </div>
    </div>
    </div>
      <transition name="fade">
        <div v-if="tooltipVisible" :style="{ top: tooltipY + 'px', left: tooltipX + 'px' }" class="tooltip">
          <h3>{{ tooltipTitle }}</h3>
          <p>{{ tooltipText }}</p>
        </div>
      </transition>
      <!--  -->
      <div id="viewDiv">
      </div>
      <div id="timeSliderDiv" class="time-slider" :style="{ maxHeight: showTimeSlider ? '30%' : '0%' }">
      </div>
      <div v-if="showFeatureTable" id="featureTableDiv" class="feature-table"></div>
    </div>
</template>

<script>
import { markRaw } from 'vue'
import Map from '@geoscene/core/Map.js'
import MapView from '@geoscene/core/views/MapView.js'
import SpatialReference from '@geoscene/core/geometry/SpatialReference.js'
import FeatureLayer from '@geoscene/core/layers/FeatureLayer.js'
import TileInfo from '@geoscene/core/layers/support/TileInfo.js'
import BasemapGallery from '@geoscene/core/widgets/BasemapGallery.js'
import Compass from '@geoscene/core/widgets/Compass.js'
// import { handleKeydown, updateTime, clc1, clc2, isDateDisabled, handleDateChange, onTimeInputChange, onSearchInputChange } from '@/assets/share_js/routeplanning_all'
import ScaleBar from '@geoscene/core/widgets/ScaleBar.js'
import DistanceMeasurement2D from '@geoscene/core/widgets/DistanceMeasurement2D.js'
import LayerList from '@geoscene/core/widgets/LayerList'
import TimeSlider from '@geoscene/core/widgets/TimeSlider.js' //  TimeSlider
import FeatureTable from '@geoscene/core/widgets/FeatureTable.js'

const FEATURE_LAYER_URL = 'https://www.geosceneonline.cn/server/rest/services/Hosted/result_2024_12_15_10min/FeatureServer'

export default {
  name: 'DatamanagerView',
  data () {
    return {
      resultLayer: null,
      toolboxIcon: require('@/assets/image/gis_dev_zgw_img/Toolbox_4868.png'), // 
      BasemapName: '',
      ismaploading: true,

      featureTable: null,
      showFeatureTable: false,
      showToolboxPopup: false, // 
      showLoadDataPopup: false, // 
      showTimeSlider: false, // 
      timeSliderDisabled: true, // 
      timeSlider: null, //  TimeSlider 

      showKernelDensityPopup: false,
      kernelRadius: 2,
      weightField: '',
      maxValue: 100,
      colorStops: '0,255,255,255,0; 0.1,0,0,255,0.8; 0.3,0,255,255,0.8; 0.5,0,255,0,0.8; 0.7,255,255,0,0.8; 0.9,255,0,0,0.8; 1,139,0,0,0.8',
      availableFields: ['result', 't08:50:00'],
      kernelDensityLayer: null,

      tooltipVisible: false,
      tooltipTitle: '',
      tooltipText: '',
      tooltipX: 0,
      tooltipY: 0,
      tooltipTimer: null
    }
  },
  watch: {
    // watch
    startTooltipTimer (title, text, event) {
      this.tooltipTitle = title
      this.tooltipText = text
      this.tooltipX = event.clientX + 10
      this.tooltipY = event.clientY + 10
      this.tooltipTimer = setTimeout(() => {
        this.tooltipVisible = true
      }, 1000) // 
    },
    hideTooltip () {
      clearTimeout(this.tooltipTimer)
      this.tooltipVisible = false
    }
  },
  methods: {
    startTooltipTimer (title, text, event) {
      this.tooltipTitle = title
      this.tooltipText = text
      this.tooltipX = event.clientX + 10
      this.tooltipY = event.clientY + 10
      this.tooltipTimer = setTimeout(() => {
        this.tooltipVisible = true
      }, 1000) // 
    },
    hideTooltip () {
      clearTimeout(this.tooltipTimer)
      this.tooltipVisible = false
    },
    toggleToolbox () {
      this.showToolboxPopup = !this.showToolboxPopup
    },
    openKernelDensityPopup () {
      this.showKernelDensityPopup = true
    },
    applyKernelDensity () {
      this.showKernelDensityPopup = false
      // 
      const colorStopsArray = this.colorStops.split(';').map(stop => {
        const [ratio, r, g, b, a] = stop.split(',').map(Number)
        return { ratio, color: `rgba(${r}, ${g}, ${b}, ${a})` }
      })

      // 
      const isTimeField = /^t\d{2}_\d{2}_\d{2}$/.test(this.weightField)

      // 
      this.kernelDensityLayer = markRaw(new FeatureLayer({
        url: FEATURE_LAYER_URL, //  URL
        title: '',
        definitionExpression: isTimeField ? `${this.weightField} = 1` : '', // 0
        renderer: {
          type: 'heatmap',
          colorStops: colorStopsArray,
          blurRadius: this.kernelRadius,
          maxPixelIntensity: isTimeField ? 1 : this.maxValue, // 
          field: this.weightField
        }
      }))

      this.mapView.map.add(this.kernelDensityLayer)
    },
    cancelKernelDensity () {
      this.showKernelDensityPopup = false
    },
    toggleTimeSlider () {
      this.timeSliderDisabled = !this.timeSliderDisabled
      this.showTimeSlider = !this.showTimeSlider
      if (this.timeSlider) {
        this.timeSlider.disabled = this.timeSliderDisabled
        this.timeSlider.renderNow() // 
      }
      if (this.timeSliderDisabled && this.resultLayer) {
        this.resultLayer.definitionExpression = '' // 
        this.resultLayer.renderer = {
          type: 'class-breaks', // 
          field: 'result', //  'result' 
          classBreakInfos: [
            {
              minValue: 0,
              maxValue: 10,
              symbol: {
                type: 'simple-marker',
                style: 'circle',
                color: '#00FF00', // 
                size: 6,
                outline: {
                  color: 'white',
                  width: 1
                }
              },
              label: '0 - 10'
            },
            {
              minValue: 10,
              maxValue: 50,
              symbol: {
                type: 'simple-marker',
                style: 'circle',
                color: '#FFFF00', // 
                size: 6,
                outline: {
                  color: 'white',
                  width: 1
                }
              },
              label: '10 - 50'
            },
            {
              minValue: 50,
              maxValue: 100,
              symbol: {
                type: 'simple-marker',
                style: 'circle',
                color: '#FFA500', // 
                size: 6,
                outline: {
                  color: 'white',
                  width: 1
                }
              },
              label: '50 - 100'
            },
            {
              minValue: 100,
              maxValue: Infinity,
              symbol: {
                type: 'simple-marker',
                style: 'circle',
                color: '#FF0000', // 
                size: 6,
                outline: {
                  color: 'white',
                  width: 1
                }
              },
              label: '> 100'
            }
          ]
        }
        this.resultLayer.refresh() // 
      }
    },
    openLoadDataPopup () {
      this.showLoadDataPopup = true
    },
    confirmLoadData () {
      // TODO: 
      this.showLoadDataPopup = false
    },
    cancelLoadData () {
      this.showLoadDataPopup = false
    },
    // 
    initMap () {
      const map = new Map({
        basemap: 'tianditu-vector'
      })
      const spatialReference = new SpatialReference({
        wkid: 4326
      })
      const tileInfo = TileInfo.create({
        spatialReference,
        numLODs: 32
      })
      this.createMapView(map, tileInfo)
      //  FeatureTable 
      this.$nextTick(() => {
        this.featureTable = new FeatureTable({
          view: this.mapView,
          layer: this.resultLayer,
          container: 'featureTableDiv'
        })
        this.$nextTick(() => {
          const featureTableDiv = document.querySelector('.feature-table')
          if (featureTableDiv) {
            featureTableDiv.style.height = '0'
          }
        })
      })
    },
    toggleFeatureTable () {
      this.showFeatureTable = !this.showFeatureTable
      this.$nextTick(() => {
        const featureTableDiv = document.querySelector('.feature-table')
        if (this.showFeatureTable) {
          if (featureTableDiv) {
            featureTableDiv.style.height = '100%'
            featureTableDiv.style.width = '100%'
          }
          const layerList = document.querySelector('.geoscene-layer-list')
          if (layerList) {
            layerList.style.maxHeight = '0'
          }
          const basemapGallery = document.querySelector('.geoscene-basemap-gallery')
          if (basemapGallery) {
            basemapGallery.style.maxHeight = '0'
          }
        } else {
          if (featureTableDiv) {
            featureTableDiv.style.height = '0'
            featureTableDiv.style.width = '0'
          }
          const layerList = document.querySelector('.geoscene-layer-list')
          if (layerList) {
            layerList.style.maxHeight = ''
          }
          const basemapGallery = document.querySelector('.geoscene-basemap-gallery')
          if (basemapGallery) {
            basemapGallery.style.maxHeight = ''
          }
        }
      })
    },
    generateFieldInfos () {
      const fields = this.resultLayer.fields.map(field => field.name)
      // 
      // console.log(fields)
      const timeFields = fields.filter(field => /^t\d{2}_\d{2}_\d{2}$/.test(field))

      const fixedFields = [
        { fieldName: 'pid', label: 'PID' },
        { fieldName: 'result', label: '' },
        { fieldName: '50lon', label: '' },
        { fieldName: '50lat', label: '' },
        { fieldName: 'near_fid', label: 'FID' },
        { fieldName: 'yaw', label: '' }
      ]

      const timeFieldInfos = timeFields.map(field => ({
        fieldName: field,
        label: field.replace('t', '').replace(/_/g, ':')
      }))

      // 
      return {
        fieldInfos: [...timeFieldInfos, ...fixedFields],
        allFields: fields
      }
    },
    // 
    createMapView (map, tileInfo) {
      //  FeatureLayer 
      const featureLayer = new FeatureLayer({
        url: 'https://www.geosceneonline.cn/server/rest/services/Hosted/wuhan_village/FeatureServer',
        title: '',
        renderer: {
          type: 'simple', // 
          symbol: {
            type: 'simple-fill', // 
            color: [0, 0, 0, 0], // 
            outline: {
              color: [0, 0, 0, 1], // 
              width: 1
            }
          }
        },
        popupEnabled: false // 
        // popupTemplate: {
        //   content: [{
        //     type: 'fields',
        //     fieldInfos: [{
        //       fieldName: 'name',
        //       label: ''
        //     }]
        //   }]
        // }
      })
      //  markRaw  resultLayer
      this.resultLayer = markRaw(new FeatureLayer({
        // url: 'https://www.geosceneonline.cn/server/rest/services/Hosted/result_2024_12_15_10min/FeatureServer',
        url: FEATURE_LAYER_URL,
        title: '',
        renderer: {
          type: 'class-breaks', // 
          field: 'result', //  'result' 
          classBreakInfos: [
            {
              minValue: 0,
              maxValue: 10,
              symbol: {
                type: 'simple-marker',
                style: 'circle',
                color: '#00FF00', // 
                size: 6,
                outline: {
                  color: 'white',
                  width: 1
                }
              },
              label: '0 - 10'
            },
            {
              minValue: 10,
              maxValue: 50,
              symbol: {
                type: 'simple-marker',
                style: 'circle',
                color: '#FFFF00', // 
                size: 6,
                outline: {
                  color: 'white',
                  width: 1
                }
              },
              label: '10 - 50'
            },
            {
              minValue: 50,
              maxValue: 100,
              symbol: {
                type: 'simple-marker',
                style: 'circle',
                color: '#FFA500', // 
                size: 6,
                outline: {
                  color: 'white',
                  width: 1
                }
              },
              label: '50 - 100'
            },
            {
              minValue: 100,
              maxValue: Infinity,
              symbol: {
                type: 'simple-marker',
                style: 'circle',
                color: '#FF0000', // 
                size: 6,
                outline: {
                  color: 'white',
                  width: 1
                }
              },
              label: '> 100'
            }
          ]
        },
        popupTemplate: {
          title: '{road_name} - {result}', //  road_name  result
          content: [{
            type: 'fields',
            fieldInfos: []
          }]
        }
      }))

      //  FeatureLayer 
      map.add(this.resultLayer)
      map.add(featureLayer)

      //  MapView 
      const mapView = new MapView({
        map: map,
        center: [114.3, 30.7], // 
        zoom: 4,
        container: 'viewDiv',
        constraints: {
          geometry: {
            type: 'extent',
            xmin: 113.6,
            ymin: 29.9,
            xmax: 115.0,
            ymax: 31.3
          },
          minScale: 500,
          maxScale: 2000000,
          rotationEnabled: false,
          lods: tileInfo.lods,
          snapToZoom: false
        }
      })
      //  `when`  `ismaploading`  false
      mapView.when(() => {
        this.ismaploading = false
        //  popupTemplate
        this.resultLayer.load().then(() => {
          const { fieldInfos, allFields } = this.generateFieldInfos(this.resultLayer.fields)
          this.resultLayer.popupTemplate.content[0].fieldInfos = fieldInfos
          this.availableFields = allFields //  availableFields
        })
      })
      //  BasemapGallery 
      const basemapGallery = new BasemapGallery({
        view: mapView,
        source: {
          query: {
            title: '"Basemaps for Developers" AND owner:geoscenedev'
          }
        }
      })
      // 
      basemapGallery.watch('activeBasemap', (newBasemap) => {
        this.handleBasemapChange(newBasemap)
      })
      //  LayerList 
      const layerList = new LayerList({
        view: mapView
      })
      //  Compass 
      const compass = new Compass({
        view: mapView
      })
      //  ScaleBar 
      const scaleBar = new ScaleBar({
        view: mapView,
        unit: 'metric', // 
        style: 'ruler' // 
      })
      //  DistanceMeasurement2D 
      const distanceMeasurement2D = new DistanceMeasurement2D({
        view: mapView,
        unit: 'metric',
        unitOptions: {
          metric: ['kilometers', 'meters'],
          nonMetric: ['miles', 'feet']
        },
        iconClass: 'esri-icon-measure-line' // 
      })
      //  BasemapToggle 
      // const basemapToggle = new BasemapToggle({
      //   view: mapView,
      //   nextBasemap: 'tianditu-image',
      //   basemap: 'tianditu-vector'
      // })
      // mapView.ui.add(basemapToggle, 'bottom-right')
      //  BasemapGallery 
      mapView.ui.add(basemapGallery, {
        position: 'bottom-right',
        index: 0
      })
      //  LayerList 
      mapView.ui.add(layerList, {
        position: 'bottom-right',
        index: 1
      })
      //  DistanceMeasurement2D 
      mapView.ui.add(distanceMeasurement2D, {
        position: 'bottom-leading',
        index: 0 // 
      })
      // 
      mapView.ui.move('zoom', {
        position: 'bottom-left',
        index: 1
      })
      // 
      mapView.ui.add(compass, {
        position: 'bottom-left',
        index: 2
      })
      //  ScaleBar 
      mapView.ui.add(scaleBar, {
        position: 'bottom-left',
        index: 3
      })

      //  TimeSlider 
      const timeSlider = markRaw(new TimeSlider({
        title: '',
        container: 'timeSliderDiv',
        view: mapView,
        disabled: this.timeSliderDisabled,
        mode: 'instant',
        layout: 'compact',
        fullTimeExtent: {
          start: new Date(2024, 11, 15, 7, 20, 0),
          end: new Date(2024, 11, 15, 17, 20, 0)
        },
        timeExtent: {
          start: new Date(2024, 11, 15, 7, 20, 0),
          end: new Date(2024, 11, 15, 7, 20, 0)
        },
        stops: {
          interval: {
            unit: 'minutes',
            value: 10
          },
          playRate: 500
        }
      }))
      this.timeSlider = timeSlider // 
      mapView.ui.add(timeSlider, 'bottom-left')

      //  `timeSlider.watch` 
      //   timeSlider.watch('timeExtent', async (value) => {
      //     const selectedTime = value.start
      //     const hours = selectedTime.getHours().toString().padStart(2, '0')
      //     const minutes = selectedTime.getMinutes().toString().padStart(2, '0')
      //     const fieldName = `t${hours}:${minutes}:00`
      //     // 1
      //     resultLayer.definitionExpression = `"${fieldName}" = 1`
      //     // 
      //     try {
      //       await resultLayer.refresh()
      //     } catch (error) {
      //       console.error(':', error)
      //     }
      //   })

      //  `timeSlider.watch` 
      timeSlider.watch('timeExtent', async (value) => {
        const selectedTime = value.start
        const hours = selectedTime.getHours().toString().padStart(2, '0')
        const minutes = selectedTime.getMinutes().toString().padStart(2, '0')
        const fieldName = `t${hours}:${minutes}:00`

        if (this.resultLayer) {
          // 
          this.resultLayer.renderer = {
            type: 'unique-value',
            field: fieldName,
            uniqueValueInfos: [
              {
                value: 1,
                symbol: {
                  type: 'simple-marker',
                  style: 'circle',
                  color: '#149ece',
                  size: 6,
                  outline: {
                    color: 'white',
                    width: 1
                  }
                },
                label: ''
              },
              {
                value: 2,
                symbol: {
                  type: 'simple-marker',
                  style: 'circle',
                  color: 'transparent',
                  size: 6,
                  outline: {
                    color: 'transparent',
                    width: 1
                  }
                },
                label: ''
              }
            ],
            defaultSymbol: {
              type: 'simple-marker',
              style: 'circle',
              color: 'transparent',
              size: 6,
              outline: {
                color: 'transparent',
                width: 1
              }
            }
          }

          // 
          try {
            await this.resultLayer.refresh()
          } catch (error) {
            console.error(':', error)
          }
        } else {
          console.error('resultLayer ')
        }
      })
      this.mapView = mapView
      const BasemapName = this.mapView.map.basemap.title
      // BasemapName 
      const basemapMapping = {
        '-': 'tianditu-vector',
        '-': 'tianditu-image',
        '-': 'tianditu-topography'
      }
      this.BasemapName = basemapMapping[BasemapName] || BasemapName
      return mapView
    },
    // 
    handleBasemapChange (basemap) {
      const basemapMapping = {
        '-': 'tianditu-vector',
        '-': 'tianditu-image',
        '-': 'tianditu-topography'
      }
      //  basemap.title 
      if (basemapMapping[basemap.title]) {
        this.BasemapName = basemapMapping[basemap.title]
      } else {
        this.BasemapName = basemap.title
      }
      const urlParams = new URLSearchParams(window.location.search)
      urlParams.set('BasemapLayer', this.BasemapName)
      window.history.replaceState({}, '', `${window.location.pathname}?${urlParams}`)
    },
    created () {
      const BasemapLayer = this.$route.query.BasemapLayer
      const basemapMapping = {
        '-': 'tianditu-vector',
        '-': 'tianditu-image',
        '-': 'tianditu-topography'
      }

      // 
      if (BasemapLayer) {
        //  BasemapLayer 
        if (basemapMapping[BasemapLayer]) {
          this.BasemapName = basemapMapping[BasemapLayer]
        } else {
          this.BasemapName = BasemapLayer
        }
      }
    }
  },
  mounted () {
    // mounted
    this.initMap()
  }

}
</script>

<style>
#viewDiv {
  position: absolute; /*  */
  top: 0; /*  */
  bottom: 0;
  left: 0;
  right: 0;
  height:100%; /* 100% */
  width: 100%; /* 100% */
  z-index: -1; /* z-indexApp.vue */
  margin: auto;
}

.feature-table {
  position: fixed;
  bottom: 0;
  right: 0;
  width: 60%;
  height: 0;
  z-index: 1000;
  background-color: rgba(255, 255, 255, 0.9);
  border: 1px solid rgba(222, 222, 222, 0.45);
  border-radius: 10px;
  overflow: hidden;
  transition: height 0.3s, width 0.3s;
}

.toolbox {
  position: absolute;
  top: 1.4%;
  left: 5px;
  height: 5.7%;
  display: flex;
  align-items: center;
  cursor: pointer;
  padding: 5px;
  border-radius: 5px;
  z-index: 1000; /*  */

  background-color: rgba(255, 255, 255, 0.5) !important; /*  */
